import { Popup, Picker, Cell } from "vant";
import { getAgeArr } from "../lib/enums";
const ageArr = getAgeArr();
const ages = {
    男: ageArr,
    女: ageArr
};
export default {
    name: "Header",
    components: {
        [Picker.name]: Picker,
        [Popup.name]: Popup,
        [Cell.name]: Cell
    },
    props: {
        genderAndAge: {
            type: Object,
            default: ()=>{}
        }
    },
    data() {
        return {
            columns: [
                {
                    values: Object.keys(ages),
                    className: "column1"
                },
                {
                    values: ages["男"],
                    className: "column2",
                    defaultIndex: 0
                }
            ],
            showPicker: false
        };
    },

    methods: {
        showPopup() {
            this.showPicker = true;
        },
        onChange(picker, values) {
            this.genderAndAge.gender = values[0];
            this.genderAndAge.age = values[1];
            this.$emit('selectChange',this.genderAndAge)
        },
        onConfirm(value, index) {
            this.showPicker = false;
        }
    }
};



