import Header from "../components/Header";
import InputBox from "../components/InputBox";
import Nav from "../components/Nav";
import { autoResponse, getTip } from "../lib/autoResponse";
import {
    inquiry,
    querySymptomByPage,
    questionDiseaseBySymptoms,
    recommendQuestion,
    recommendDisease,
    recommendDrug
} from "../service/index";
import { Button, ActionSheet } from "vant";

export default {
    name: "Main",
    components: {
        Header,
        InputBox,
        Nav,
        [Button.name]: Button,
        [ActionSheet.name]: ActionSheet
    },
    data() {
        return {
            navList: [
                {
                    icon: "icon-zhineng",
                    type: "智能导诊",
                    path: "/guidance",
                    id: 0
                },
                {
                    icon: "icon-duihua",
                    type: "智能问病",
                    path: "/askingDisease",
                    id: 1
                },
                {
                    icon: "icon-yao",
                    type: "药物查询",
                    path: "drugInquiry",
                    id: 2
                },
                {
                    icon: "icon-fankui",
                    type: "意见反馈",
                    path: "/feedback",
                    id: 3
                }
            ],
            arr: [],
            navItem: {},
            age: 24,
            gender: "男",
            pageCount: 1,
            symptomId: "",
            inputText: "",
            offsetTop: 0,
            genderAndAge: {
                age: 24,
                gender: "男"
            },
            isSelect: -1,
            pageNum: 1,
            pageSize: 6,
            show: false,
            symptomObj: {}
        };
    },

    methods: {
        // 年龄性别变更
        ageChange(params) {
            this.age = params.age;
            this.gender = params.gender;
        },
        // 头部导航
        nav(item) {
            this.navItem = item;
            if (item.type != "意见反馈") {
                this._response(item, "");
            } else {
                window.location.href =
                    "http://res.zwjk.com/h5-test/open-application-front/prod/#/feedbackTypes?type=1&router=feedbackTypes&hospitalId=08adfbab-06f6-48df-9d3c-ed76e81bcb01&wechatConfigToken=f78c6ace-9942-4051-9d90-0c9ccab9f754&projectid=4933a927-fae0-4a77-8ead-dadf678af195&applicationId=3a385635-a88a-41a3-9ef7-a1254d1bg564";
            }
        },
        // 输入文本发送
        send(inputText) {
            this.inputText = inputText;
            if (inputText != "") {
                this.arr.push({
                    type: "C",
                    icon: "icon-tubiao_guke",
                    content: inputText
                });
                // this.getHeight();
                this._getInquryType(inputText);
            }
        },
        // 自动应答
        _response(item, inputText) {
            let obj = autoResponse(item, inputText);
            this._queryRecommend(item.id).then(res => {
                this.arr.push({
                    source: "U",
                    icon: "icon-customer",
                    content: obj,
                    recommendList: res.data.data
                });
                this.getHeight();
            });
        },
        // 获取推荐
        _queryRecommend(id) {
            return new Promise((resolve, reject) => {
                if (id == 0) {
                    recommendQuestion().then(res => {
                        resolve(res);
                    });
                } else if (id == 1) {
                    recommendDisease().then(res => {
                        resolve(res);
                    });
                } else if (id == 2) {
                    recommendDrug().then(res => {
                        resolve(res);
                    });
                }
            });
        },
        // 下一批
        nextPatch(index) {
            this._queryRecommend(this.navItem.id).then(res => {
                let obj = autoResponse(this.navItem, "");
                this.arr[index].recommendList = res.data.data;
                localStorage.setItem("arr", JSON.stringify(this.arr));
                this.getHeight();
            });
        },
        // 问诊类型
        _getInquryType(inputValue) {
            let params = {
                inputText: inputValue,
                wechatConfigToken: 1
                // localStorage.getItem("wechatConfigToken")?localStorage.getItem("wechatConfigToken"):1
            };
            inquiry(params).then(res => {
                if (res.data.type == 3) {
                    this.show = true;
                    this.symptomObj = res.data;
                    this.$set(
                        this.symptomObj,
                        "symptomList",
                        this.symptomObj.selectSymptomList
                    );
                    this.arr.push(
                        Object.assign(res.data, {
                            source: "U"
                        })
                    );
                    localStorage.setItem("arr", JSON.stringify(this.arr));
                    this.getHeight();
                } else if (
                    res.data.type == 0 &&
                    !res.data.diagnoseResult &&
                    !res.data.diseaseList &&
                    !res.data.drugList
                ) {
                    this._queryRecommend(this.navItem.id).then(res => {
                        let recommendList = res.data.data;
                        let notFoundTip = getTip(this.navItem.id);
                        this.arr.push({
                            source: "U",
                            recommendList: res.data.data,
                            notFoundTip: notFoundTip,
                            type: 0
                        });
                    });
                    localStorage.setItem("arr", JSON.stringify(this.arr));
                    this.getHeight();
                } else {
                    this.arr.push(
                        Object.assign(res.data, {
                            source: "U"
                        })
                    );
                    localStorage.setItem("arr", JSON.stringify(this.arr));
                    this.getHeight();
                }
            });
        },
        // 查看更多疾病
        checkMoreDisease(diseaseList) {
            this.$router.push({
                path: "/moreDisease",
                query: {
                    diseaseList: diseaseList
                }
            });
        },
        // 查看疾病詳情
        checkDetail(item) {
            this.$router.push({
                path: "/diseaseDetail",
                query: {
                    diseaseObj: item
                }
            });
        },
        // 查看更多药物
        checkMoreDrug(drugList) {
            this.$router.push({
                path: "/moreDrug",
                query: {
                    drugList: drugList
                }
            });
        },
        // 查看药物详情
        checkDrugDetail(url) {
            window.location.href = url;
        },
        // 选择症状
        selectSymptom(symptomObj, item, index) {
            this.isSelect = index;
            this.symptomObj.currentSymptom.push(item);
            let params = {
                symptomNames: this.symptomObj.currentSymptom
            };
            questionDiseaseBySymptoms(params).then(res => {
                if (res.data.end) {
                    this.show = false;
                    this.arr.push(
                        Object.assign({
                            source: "U",
                            diseaseList: res.data.diseaseNodes,
                            type: 1,
                            diagnoseResult: "11"
                        })
                    );
                    localStorage.setItem("arr", JSON.stringify(this.arr));
                } else {
                    this.$set(
                        this.symptomObj,
                        "symptomList",
                        res.data.selectSymptomList
                    );
                }
            });
        },
        nextPage() {
            this.pageNum = this.pageNum + 1;
            let params = {
                uuid: this.symptomObj.uuid,
                pageNum: this.pageNum,
                pageSize: this.pageSize
            };
            querySymptomByPage(params).then(res => {
                this.symptomObj = res.data;
                this.$set(
                    this.symptomObj,
                    "symptomList",
                    this.symptomObj.symptomNames
                );
            });
        },
        // 获取高度
        getHeight() {
            let arr = document.getElementsByClassName("left-box");
            if (arr.length > 0) {
                let height = arr[arr.length - 1].offsetTop;
                console.log('height',height)
                this.offsetTop = this.offsetTop + 2*height;
                let top = this.offsetTop ? this.offsetTop : height;
                window.scrollTo({
                    top: top,
                    behavior: "smooth"
                });
            }
        }
    },
    created() {
        if (!localStorage.getItem("wechatConfigToken")) {
            localStorage.setItem(
                "wechatConfigToken",
                this.$route.query.wechatConfigToken
            );
        }
        if (
            localStorage.getItem("arr") &&
            Object.keys(localStorage.getItem("arr")).length
        ) {
            this.arr = JSON.parse(localStorage.getItem("arr"));
            this.getHeight();
        }else{
            this.nav(this.navList[0]);
        }
    }
};



